.trades .table--wrapper +* {
    margin-top: 15px;
}

.trades th {
    text-align: center;
}

.trades tr.recent {
    background-color: rgba($green, 0.3);
    animation: fadeHighlight 2s ease-in-out 1.2s forwards;
}

.trades tr.canceled td, .trades tr.expired td {
    opacity: 0.6;
}

.trades td.volume,
.trades td.price,
.trades td.match {
    overflow: hidden;
    text-overflow: ellipsis;

    &:hover {
        text-overflow: clip;
    }
}

.trades td.volume {
    max-width: 120px;
}

.trades td.price {
    max-width: 220px;
}

.trades .cross {
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;
}

.trades .loading:after {
    height: 15px;
    width: 15px;
    border-width: 1px;
}